<span class="token variable">$pageRows</span> <span class="token operator">=</span> <span class="token number">10</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">nearby_poi</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token variable">$page</span><span class="token punctuation">,</span> <span class="token variable">$pageRows</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
